/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

input {
  font-size: 2.4rem;
}

input:focus, textarea:focus, select:focus {
  outline: none;
}

body {
  font-family: "Space Mono", monospace;
  font-size: 1.8rem;
  font-weight: 300;
}

h1, h2, h3, h4, h5, li {
  font-family: "Space Mono", monospace;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.hidden {
  display: none;
  overflow: hidden;
}

.mg-top-5 {
  margin-top: 5rem;
}

.mg-top-10 {
  margin-top: 10rem;
}

.textCenter {
  text-align: center;
}

.containerXl {
  width: 95%;
  max-width: 1800px;
  margin: 0 auto;
}

button {
  all: unset;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.main {
  height: 100vh;
  background-color: hsl(185deg, 41%, 84%);
}
.main__heading {
  text-align: center;
  margin: 0;
  padding: 4rem;
  text-transform: uppercase;
  color: hsl(186deg, 14%, 43%);
  font-size: 2.4rem;
  letter-spacing: 1rem;
  line-height: 1.5;
  font-weight: bold;
}
@media (min-width: 768px) {
  .main__heading {
    margin-bottom: 5rem;
  }
}

.calculator {
  background-color: #FFFFFF;
  padding: 4rem 2rem;
  width: 100%;
  border-radius: 2rem;
}
.calculator__text {
  color: hsl(186deg, 14%, 43%);
  font-weight: bold;
  margin: 0;
  padding: 1rem 0rem;
  margin-bottom: 1rem;
}
.calculator__input {
  background-color: hsl(189deg, 41%, 97%);
  border: none;
  text-align: right;
  appearance: textfield;
  padding: 1rem;
  width: 100%;
  border-radius: 1rem;
  background-image: url(../img/icon-dollar.svg);
  background-repeat: no-repeat;
  background-position: center left 5%;
  margin-bottom: 1rem;
  color: hsl(183deg, 100%, 15%);
  font-weight: bold;
  font-family: "Space Mono", monospace;
}
.calculator__input--ppl {
  background-image: url(../img/icon-person.svg);
}
.calculator__percents {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  width: 100%;
  gap: 2rem;
}
.calculator__btn {
  width: 95%;
  height: 100%;
  place-self: center;
  text-align: center;
  background-color: hsl(183deg, 100%, 15%);
  color: #FFFFFF;
  border-radius: 0.5rem;
  font-weight: bold;
  font-size: 2rem;
  padding: 3px 0;
  cursor: pointer;
}
.calculator__btn:hover {
  transition: all 0.5s;
  background-color: hsl(185deg, 41%, 84%);
  color: hsl(183deg, 100%, 15%);
}
.calculator__btn--mod {
  background-color: hsl(189deg, 41%, 97%);
  color: hsl(183deg, 100%, 15%);
  appearance: textfield;
  cursor: default;
}
.calculator__btn--mod:hover {
  background-color: hsl(189deg, 41%, 97%);
}
.calculator__display {
  margin-top: 3rem;
  background-color: hsl(183deg, 100%, 15%);
  padding: 3rem;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.calculator__amount {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.calculator__person {
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: bold;
  margin: 0;
}
.calculator__person span {
  font-size: 1.2rem;
  display: block;
  color: hsl(186deg, 14%, 43%);
  font-weight: bold;
  margin-bottom: 2rem;
}
.calculator__prize {
  font-weight: bold;
  color: hsl(172deg, 67%, 45%);
  font-size: 3rem;
  margin: 0;
}
.calculator__reset {
  text-align: center;
  align-self: center;
  margin-top: rem;
  padding: 1.5rem 0;
  border-radius: 1rem;
  text-transform: uppercase;
  cursor: pointer;
  width: 100%;
  background-color: hsl(172deg, 67%, 45%);
  font-weight: bold;
  color: hsl(183deg, 100%, 15%);
}
.calculator__reset:hover {
  transition: all 0.5s;
  background-color: hsl(185deg, 41%, 84%);
}
@media (min-width: 768px) {
  .calculator {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
    height: 60%;
    padding: 3rem 3rem;
  }
  .calculator__display {
    padding: 5rem 5rem;
  }
  .calculator__inputs {
    padding-left: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
  .calculator__percents {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    width: 90%;
    gap: 2rem;
  }
  .calculator__btn {
    width: 90%;
  }
  .calculator__amount {
    margin-bottom: 1rem;
  }
  .calculator__person span {
    margin: 0;
  }
  .calculator__display {
    justify-content: flex-start;
    margin-top: 0;
  }
  .calculator__reset {
    margin-top: auto;
  }
  .calculator__prize {
    font-size: 5rem;
  }
  .calculator__text {
    margin: 0;
    padding: 0;
  }
}